@font-face{
    font-family: 'Roboto';
    src: url(fonts/Roboto-Medium.ttf);
}

@font-face{
    font-family: 'Garamond';
    src: url(EBGaramond-Regular.ttf);
}

@font-face{
    font-family: 'Roboto Mono';
    src: url(fonts/RobotoMono-Regular.ttf);
}

@font-face{
    font-family: 'Rubic';
    src: url(fonts/Rubik-Regular.ttf);
}

@font-face{
    font-family: 'Manrope';
    src: url(fonts/Manrope-ExtraBold.ttf);
}

$mainFont: 'Manrope';
$regularFont: Garamond;

@mixin regular{
    font-family: $regularFont;
    font-size: 1.4rem;
}

@mixin regular-small{
    font-family: $regularFont;
    font-size: 1.1rem;
}

@mixin main{
    font-family: $mainFont;
    font-size: 1.0rem;
    font-weight: bold;
}

$alertColor: #FF2A00;
$regularColor: #C9EC23;
$primeColor: #00171F;
$my-prime-color: rgba(0, 0, 0, 0.8);
$auxColor: #E4EDF1;

$width: 60rem;

p, h1, h2, h3{
    margin: 0;
    padding: 0;
}

.app-wrapper{
    color: $my-prime-color;
    position: relative;
    //overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    &.show-calc-app{
        height: 100vh;
        overflow: hidden;
    }
    .app{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        width: $width;
        @media only all and (max-width: 950px){
            width: auto;
        }
        .menu-bar{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }
    }
}