@import '../utils.scss';
@import '../App.scss';

.footer{
    background-color: $primeColor;
    padding: 3.5em 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .footer-inner{
        width: $width;
        @media only all and (max-width: 1040px){
            width: auto;
        }
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .footer-logo{
            @media only all and (max-width: 700px){
                padding: 0 1rem;
            }
        }
        .footer-info{
            display: flex;
            flex-direction: column;
            justify-content: center;
            .text-wrapper{
                width: 28em;
                text-align: end;
                @include regular-small;
                color: $auxColor;
                @media only all and (max-width: 700px){
                    width: auto;
                    padding: 0 1rem;
                }
            }
        }
    }
    .icons-info{
        color: $auxColor;
        width: $width;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        @media only all and (max-width: 1040px){
            width: 650px;
        }
        @media only all and (max-width: 700px){
            width: auto;
        }
        div{
            a{
                color: $regularColor;
                margin: 0 0.5rem;
                text-decoration: none;
            }
        }
    }
}