@import '../../App.scss';
@import '../../keyframes.scss';

$transitiionPoint: 40em;

@include keyframes(menu-panel-animation) {
    0% {
        transform: translateX(30px);
        opacity: 0;
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.menu-panel{
    display: flex;
    align-items: center;
    position: relative;
    animation: menu-panel-animation 1s linear;
    .shortcut{
        display: none;
        @media only all and (max-width: $transitiionPoint){
            display: flex;
            padding: 0 1.5em;
            font-family: $mainFont;
            padding: 0.4rem 0.5rem;
            cursor: pointer;
            &:hover{
                background-color: $regularColor;
                color: black;
            }
        }
    }
    .full{
        @media only all and (max-width: $transitiionPoint){
            display: none;
            &.show{
                width: auto;
                //padding: 0 1em;
                position: absolute;
                display: flex;
                flex-direction: column;
                top: 3.5em;
                left: -0.9rem;
                text-align: center;
                background-color: $auxColor;
            }
        }
        width: 25rem;
        display: flex;
        justify-content: space-between;
    }
}