@import '../App.scss' ;

.video-panel{
    -webkit-perspective: 1000;
    .video-tiles{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        @media only all and (max-width: 680px){
            justify-content: center;
        }
    }
    .load-wrapper{
        display: flex;
        flex-direction: row;
        justify-content: center;
        .load{
            @include regular;    
            &:hover{
                color: $alertColor;
                cursor: pointer;
            }
            &.none{
                display: none;
            }
        }
    }
    @media only all and (max-width: 1040px){
        width: 630px;
    }
    @media only all and (max-width: 680px){
        width: 300px;
        padding: 0 1rem;
    }
}